import React from 'react'
import "./Appp.css"
import photo2 from "../public/images/kate2.jpg"
import photo3 from "../public/images/kate3.jpg"
import photo4 from "../public/images/kate4.jpg"
import photo5 from "../public/images/kate5.jpg"
import photo6 from "../public/images/kate6.jpg"

const Appp = () => {
    return (
        <div className="kate">
            <h2>I love you Kate</h2>
            <div className="photoswrapper">
                <img className="photos" src={photo2} />
                <img className="photos" src={photo3} />
                <img className="photos" src={photo4} />
                <img className="photos" src={photo5} />
                <img className="photos1" src={photo6} />
            </div>
        </div>
    )
}

export default Appp



